(function() {
  'use strict';

  angular
    .module('artoo', ['ngSanitize', 'ngAria', 'ui.router', 'toastr', 'ui.bootstrap', 'LocalForageModule']);

})();


(function() {
  'use strict';

  angular
    .module('artoo')
    .controller('AddUserModalController', AddUserModalController);

  /** @ngInject */
  function AddUserModalController($uibModalInstance, BugListService, RootService, user, userIndex) {
    var vm = this;

    vm.usernameError = false;
    if (user) {
      vm.username = user.username;
      vm.password = user.password;
      vm.role = user.role;
    }

    vm.roles = [
      {'lookupCode': 1, 'description': 'Admin'},
      {'lookupCode': 2, 'description': 'Prduct engineer'},
      {'lookupCode': 3, 'description': 'Support engineer'}
    ];

    vm.addNewUser = function () {
      var userDetails = {
        username: vm.username,
        password: vm.password,
        role: vm.role
      };

      if (user) {
        RootService.setUser(userDetails, userIndex);
      } else {
        RootService.setNewUser(userDetails);
      }

      $uibModalInstance.close();
    };

    vm.checkUserName = function(name) {
      var index = RootService.checkUserName(name);
      if (index > 0 && index != userIndex) {
        vm.usernameError = true;
      } else {
        vm.usernameError = false;
      }
    };

    vm.cancel = function () {
      $uibModalInstance.dismiss('cancel');
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .directive('navBar', navBar);

  /** @ngInject */
  function navBar($localForage, $window) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
        adminRole: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController() {
      var vm = this;

      vm.logout = function () {
        $localForage.removeItem('LoggedUser');
        $window.location.reload();
      }
      // "vm.creationDate" is available by directive option "bindToController: true"
      // vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();


(function() {
  'use strict';

  angular
    .module('artoo')
    .controller('AddBugModalController', AddBugModalController);

  /** @ngInject */
  function AddBugModalController($uibModalInstance, BugListService, bug, bugIndex, RootService) {
    var vm = this;

    // if edit of bug param - bug is sent to modal through resolve so add default values
    if (bug) {
      vm.bugName = bug.name;
      vm.bugDesc = bug.description;
      vm.priority = bug.priority;
      vm.status = bug.status;
      vm.assignedTo = bug.assignedTo;
    } else {
      vm.priority = 2;
      vm.status = 1;
    }

    vm.priorities = [
      {'lookupCode': 1, 'description': 'High'},
      {'lookupCode': 2, 'description': 'Medium'},
      {'lookupCode': 3, 'description': 'Low'}
    ];

    vm.statuses = [
      {'lookupCode': 1, 'description': 'Pending'},
      {'lookupCode': 2, 'description': 'Completed'}
    ];

    vm.allUsers = RootService.getAllUsers();

    // for assigning users - filter for only product engineers
    vm.allProductEngineers = vm.allUsers.filter(function (user) {
      return user.role === 2;
    });

    vm.addNewBug = function () {
      var bugDetails = {
        name: vm.bugName,
        description: vm.bugDesc,
        priority: vm.priority,
        status: vm.status,
        assignedTo: vm.assignedTo
      };

      // if bug exists its edit so, override with index.
      if (bug) {
        BugListService.saveBug(bugDetails, bugIndex);
      } else {
        BugListService.addNewBug(bugDetails);
      }

      $uibModalInstance.close();
    };

    vm.cancel = function () {
      $uibModalInstance.dismiss('cancel');
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .controller('ManageUsersController', ManageUsersController);

  /** @ngInject */
  function ManageUsersController($uibModal, appConst, RootService) {
    var vm = this;

    vm.userList = RootService.getAllUsers();
    vm.roles = appConst.roles;

    vm.userModalOpen = function (user, userIndex) {
      $uibModal.open({
        animation: vm.animationsEnabled,
        ariaLabelledBy: 'modal-title',
        ariaDescribedBy: 'modal-body',
        templateUrl: 'addNewUserModal.html',
        controller: 'AddUserModalController',
        controllerAs: 'vm',
        resolve: {
          user: user,
          userIndex: userIndex
        }
      });
    };

    vm.removeUser = function(index) {
      RootService.removeUser(index);
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .factory('RootService', RootService);

  /** @ngInject */
  function RootService($localForage) {
    var allUsers = [];
    var presentUser = {};

    //Load available users on to service on intiation
    $localForage.getItem('users').then(function(users) {
      allUsers = users;
    });

    this.getAllUsers = function () {
      return allUsers;
    };

    this.getPresentUser = function () {
      return presentUser;
    };

    this.setPresentUser = function (user) {
      presentUser = user;
    };

    this.setNewUser = function (user) {
      allUsers.push(user);
      $localForage.setItem('users', allUsers);
    };

    this.removeUser = function (index) {
      allUsers.splice(index, 1);
      $localForage.setItem('users', allUsers);
    };

    // for editing user details.
    this.setUser = function (user, index) {
      allUsers[index] = user;
    };

    this.CheckLogin = function() {
      return $localForage.getItem('LoggedUser').then(function(username) {
        if (username) {
          return allUsers.find(function (user) {
            return user.username === username;
          });
        }
        return false;
      });
    };

    this.checkUserName = function(username) {
      return allUsers.findIndex(function (user) {
        return user.username === username;
      });
    };

    return this;
  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(RootService, $uibModal, BugListService, appConst, $timeout) {
    var vm = this;
    vm.currentUser = RootService.getPresentUser();
    vm.animationsEnabled = true;
    vm.adminId = appConst.roles[1]; // better get key by value with admin
    vm.priorities = appConst.priorities;
    vm.statuses = appConst.statuses;

    if (vm.currentUser.role === 2) {
      $timeout(function () {
        vm.bugList = BugListService.getProductEngBugs(vm.currentUser.username);
      }, 100); // localForage async call back is after this function call. just waiting for stack to be cleared.
    } else {
      vm.bugList = BugListService.getBugList();
    }

    vm.bugModalOpen = function (bugObj, bugIndex) {
      $uibModal.open({
        animation: vm.animationsEnabled,
        ariaLabelledBy: 'modal-title',
        ariaDescribedBy: 'modal-body',
        templateUrl: 'addNewBugModal.html',
        controller: 'AddBugModalController',
        controllerAs: 'vm',
        resolve: {
          bug: bugObj,
          bugIndex: bugIndex
        }
      });
    }

    // class for showing priority label
    vm.priorityClass = function (priority) {
      if (priority === 1) {
        return 'label label-danger';
      } else if (priority === 2) {
        return 'label label-warning';
      } else {
        return 'label label-info';
      }
    };

    // class for showing status label
    vm.statusClass = function (status) {
      if (status === 1) {
        return 'label label-danger';
      } else {
        return 'label label-success';
      }
    };

    // function for onclick of sort - just reverse if its of same sortType or sort
    vm.sortBy = function(type) {
      vm.reverse = vm.filter === type ? !vm.reverse : vm.reverse;
      vm.filter = type;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .factory('BugListService', BugListService);

  /** @ngInject */
  function BugListService($localForage) {
    var bugsList = [];

    $localForage.getItem('bugs').then(function(bugs) {
      if (bugs) {
        Array.prototype.push.apply(bugsList, bugs);
      }
    });

    this.getBugList = function () {
      return bugsList;
    };

    this.getProductEngBugs = function (username) {
      return bugsList.filter(function(bug) {
        return bug.assignedTo == username;
      });
    };

    this.saveBug = function (bug, bugIndex) {
      bugsList[bugIndex] = bug;
      $localForage.setItem('bugs', bugsList);
    };

    this.addNewBug = function(bug) {
      bugsList.unshift(bug);
      $localForage.setItem('bugs', bugsList);
    };

    return this;
  }
})();

