(function() {
  'use strict';

  angular
    .module('artoo')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(LoginService, $localForage, RootService, $state) {
    var vm = this;

    vm.submitLoginForm = function (username, password) {
      vm.passwordError = false;
      vm.usernameError = false;

      var userMatch = RootService.getAllUsers().find(function (user) {
        return user.username === username;
      });
      if (userMatch) {
        if (userMatch.password === password) {
          RootService.setPresentUser(userMatch);
          $localForage.setItem('LoggedUser', userMatch.username).then(function() {
            $state.go('root.home');
          });
        } else {
          vm.passwordError = true;
        }
      } else {
        vm.usernameError = true;
      }
      return userMatch;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $localForage, $rootScope, $state) {

    $localForage.getItem('users').then(function(data) {
      var mockData = [{
           "username":"admin",
           "password":"admin",
           "id":1,
           "role":1
        }
      ];

      if(!data) {
        $localForage.setItem('users', mockData);
      }

    });

    $rootScope.$on('$stateChangeError', function(event, toState, toParams, fromState, fromParams, error) {
      if (error === 'routeError') {
        $state.go('root.home');
      } else if (error === 'loginError') {
        $state.go('login')
      }
    });

  }

})();

(function() {
  'use strict';

  angular
    .module('artoo')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('root', {
        url: '',
        abstract: true,
        templateUrl: 'app/root/root.html',
        resolve: {
            loginCheck: function(RootService, $q) {
              var deferred = $q.defer();
              RootService.CheckLogin().then(function (result) {
                if (result) {
                  RootService.setPresentUser(result);
                  deferred.resolve();
                }
                deferred.reject('loginError');
              });
              return deferred.promise;
            }
          }
      })
      .state('root.home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .state('root.manageUsers', {
        url: '/users',
        templateUrl: 'app/users/manageUsers.html',
        controller: 'ManageUsersController',
        controllerAs: 'vm',
        resolve: {
          adminCheck: function(RootService, $q) {
            var deferred = $q.defer();
            var user = RootService.getPresentUser();
              if (user.role !== 1) {
                deferred.reject('routeError');
              }
              deferred.resolve();
            return deferred.promise;
          }
        }
      })
      .state('login', {
        url: '/login',
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm',
        resolve: {
            loginCheck: function(RootService, $q) {
              var deferred = $q.defer();
              RootService.CheckLogin().then(function (result) {
                if (result) {
                  deferred.reject('routeError');
                }
                deferred.resolve();
              });
              return deferred.promise;
            }
          }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

